















import {Vue, Component, Emit, Prop} from 'vue-property-decorator';

@Component
export default class Category extends Vue {
	@Prop(String) category!: string;
	
	@Emit()
	toggleCategory() {
		if (this.category === '-') {
			this.$emit('update:value', '+');
		} else {
			this.$emit('update:value', '-');
		}
	}
}
